import React from 'react';
import MapContactUSComponent from './Map/Map';
import ReviewComponent from './CustomerReview/review';
import Typed from 'react-typed';
import './Contact.scss';
class ContactUSComponent extends React.Component {
    render() {
        return (
            <div>
                <div className="backgroud-feedback">
                    <div className="type-feedback">
                        <Typed
                            strings={[
                                'You need help!!!',
                                'You are in trouble',
                                'Let call us: (+84)375185272',
                                'Or you can fill out the form below',
                                'And click submit button', 
                                'We will solve your problem soon']}
                            typeSpeed={40}
                            backSpeed={50}
                            loop >
                        </Typed>
                    </div> 
                        <div className="box"></div> 
                        <div className="box"></div>  
                        <div className="box"></div>  
                        <div className="box"></div>       
                        <div className="box"></div>  
                        <div className="box"></div> 
                </div>
                <div className="container">
                    <ReviewComponent />
                    <MapContactUSComponent />
                </div>
                
            </div>
        );
    }
}
export default ContactUSComponent;