import React from 'react';
import TrendingComponent from './Trending/Trending';
import UpdateNewsComponent from './UpdateNew/updateNew';
import EndNewsComponent from './EndNews/endNews';
import PopularComponent from './Popular/popular';

class NewsComponent extends React.Component {
    render() {
        return (
            <div className="container">
              <TrendingComponent />
              <UpdateNewsComponent />
              <EndNewsComponent />
              <PopularComponent />
            </div>
        );
    }
}
export default NewsComponent;