import React from 'react';
import './Menu.scss';
import { BrowserRouter, Route, Link } from "react-router-dom";
import NewsComponent from '../News/news';
import OurServiceComponent from '../Ourservice/OurService';
import LaptopComponent from '../Laptop/Laptop';
import HomeComponent from '../Home/Home';
import ContactUSComponent from '../ContactUS/Contactus';
import LaptopAsusComponent from '../Laptop/ASUS/Asus';
import LaptopHPComponent from '../Laptop/HP/HP';
import LaptopDellComponent from '../Laptop/DELL/Dell';
import LaptopMacbookComponent from '../Laptop/Macbook/Macbook';
import TabletComponent from '../Tablet/Tablet';
import TabletSamsungComponent from '../Tablet/Samsung/TabletSamsung';
import IpadComponent from '../Tablet/IPad/Ipad';
import TabletHuaweiComponent from '../Tablet/Huawei/Huawei';
import TabletLenovoComponent from '../Tablet/Lenovo/Lenovo';
import PhoneComponent from '../Phone/Phone';
import IPhoneComponent from '../Phone/Iphone/Iphone';
import SPhoneSamsungComponent from '../Phone/Samsung/Samsung';
import SPhoneOppoComponent from '../Phone/OPPO/Oppo';
import SPhoneHuaweiComponent from '../Phone/Huawei/Huawei';
import PostComponent from '../News/Post/Post';
import Modal from 'react-awesome-modal';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

class MenuComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible : false
        }
    }
    
    openModal() {
        this.setState({
            visible : true
        });
    }
    
    closeModal() {
        this.setState({
            visible : false
        });
    }
    render() {
        return (
            <div>
                <BrowserRouter>
                    <header id="header">
                        <nav className="navbar navbar-default" role="navigation">

                            <div className="navbar-header">
                                <button type="button" className="navbar-toggle" data-toggle="collapse" data-target=".navbar-ex1-collapse">
                                    <span className="sr-only">Toggle navigation</span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                </button>
                                <Link className="navbar-brand" to="/">PPHHT</Link>
                                <form className="navbar-form navbar-left search-box" role="search">
                                    <div className="form-group">
                                        <input type="text" className="form-control" placeholder="Search" />
                                    </div>
                                    <button className="sb-menu" type="submit"><i className="fa fa-search"></i></button>
                                </form>
                            </div>


                            <div className="collapse navbar-collapse navbar-ex1-collapse">
                                <ul className="nav navbar-nav">
                                    <li className="nav-item ">

                                    <Link className="nav-link active" to="/phone">SPhone<span className="sr-only">(current)</span></Link>
                                        <ul className="sub-menu one-li-sub">
                                            <li className="fath-one"><Link to="/phone/samsung">Samsung</Link></li>
                                            <li className="fath-one"><Link to="/phone/iphone">IPhone</Link></li>
                                            <li className="fath-one"><Link to="/phone/huawei">Huawei</Link></li>
                                            <li className="fath-one"><Link to="/phone/oppo">OPPO</Link></li>
                                        </ul>
                                    </li>
                                    <li className="nav-item ">

                                        <Link className="nav-link active" to="/tablet">Tablet<span className="sr-only">(current)</span></Link>
                                        <ul className="sub-menu one-li-sub">
                                            <li className="fath-one"><Link to="/tablet/samsung">Samsung Galaxy Tab</Link></li>
                                            <li className="fath-one"><Link to="/tablet/ipad">IPad</Link></li>
                                            <li className="fath-one"><Link to="/tablet/huawei">Huawei Mediapad</Link></li>
                                            <li className="fath-one"><Link to="/tablet/lenovo">Lenovo Tab</Link></li>
                                        </ul>
                                    </li>
                                    <li className="nav-item ">

                                        <Link className="nav-link active" to="/laptop">Laptop<span className="sr-only">(current)</span></Link>
                                        <ul className="sub-menu one-li-sub">
                                            <li className="fath-one"><Link to="/laptop/asus">ASUS</Link></li>
                                            <li className="fath-one"><Link to="/laptop/hp">HP</Link></li>
                                            <li className="fath-one"><Link to="/laptop/dell">DELL</Link></li>
                                            <li className="fath-one"><Link to="/laptop/macbook">MACBOOK</Link></li>
                                        </ul>
                                    </li>
                                    <li className="nav-item ">

                                        <Link className="nav-link active" to="#">Service<span className="sr-only"></span></Link>
                                        <ul className="sub-menu one-li-sub">
                                            <li className="fath-one"><Link to="/news">News</Link></li>
                                            <li className="fath-one"><Link to="/ourservice">Our Service</Link></li>
                                            <li className="fath-one"><Link to="/contactus">ContactUS </Link> </li>
                                        </ul>
                                    </li>

                                </ul>
                                <ul className="nav navbar-nav navbar-right">
                                    <div><input type="button" className="btn-formLogin" value="Login" onClick={() => this.openModal()} /></div>
                                   
                                </ul>
                            </div>
                        </nav>
                        <Route exact path="/">
                            <HomeComponent />
                        </Route>
                        <Route exact path="/news">
                            <NewsComponent />
                        </Route>
                        <Route path="/news/post">
                            <PostComponent/>
                        </Route>
                        <Route path="/ourservice">
                            <OurServiceComponent />
                        </Route>
                        <Route path="/contactus">
                            <ContactUSComponent />
                        </Route>
                        <Route exact path="/laptop">
                            <LaptopComponent />
                        </Route>
                        <Route path="/laptop/asus">
                            <LaptopAsusComponent />
                        </Route>
                        <Route path="/laptop/hp">
                            <LaptopHPComponent />
                        </Route>
                        <Route path="/laptop/dell">
                            <LaptopDellComponent />
                        </Route>
                        <Route path="/laptop/macbook">
                            <LaptopMacbookComponent />
                        </Route>
                        <Route exact path="/tablet">
                            <TabletComponent />
                        </Route>
                        <Route path="/tablet/samsung">
                            <TabletSamsungComponent />
                        </Route>
                        <Route path="/tablet/ipad">
                            <IpadComponent />
                        </Route>
                        <Route path="/tablet/huawei">
                            <TabletHuaweiComponent />
                        </Route>
                        <Route path="/tablet/lenovo">
                            <TabletLenovoComponent />
                        </Route>
                        <Route exact path="/phone">
                            <PhoneComponent />
                        </Route>
                        <Route path="/phone/samsung">
                            <SPhoneSamsungComponent />
                        </Route>
                        <Route path="/phone/oppo">
                            <SPhoneOppoComponent />
                        </Route>
                        <Route path="/phone/huawei">
                            <SPhoneHuaweiComponent />
                        </Route>
                        <Route path="/phone/iphone">
                            <IPhoneComponent />
                        </Route>
                    </header>
                </BrowserRouter>
            </div>
        );
    }
}
export default MenuComponent;